.PostsContainer {
  position: relative;
}


.SinglePost {
  border: 1px solid gray;
  border-radius: 20px;
  margin-bottom: 20px;
  padding: 20px;
}

.SinglePostHeader {
  height: 100px;
  display: flex;
  justify-content: start;
  gap: 50px;
  align-items: center;
}

.SinglePostHeader h3 {
  font-size: 1.5em;
}

.SinglePostHeader img {
  height: 100%;
  border-radius: 100%;
  border: 5px solid rgb(28, 102, 186);
}

.SinglePostBody p {
  padding-left: 145px;
}

.SinglePostDate {
  flex: 1 1 auto;
  text-align: right;
}

.SinglePostFooter {
  text-align: right;
}

.SinglePostFooter .Button {
  margin-left: 20px;
}



.SinglePostDeleteConfirm {
  display: flex;
  flex-flow: column;
  align-items: center;
  border: 2px solid indianred;
  border-radius: 20px;
  margin-bottom: 20px;
  position: absolute;
  top: 20px;
  width: 100%;
  padding: 30px 0;
  background-color: oldlace;
  box-shadow: 5px 5px 10px rgb(0 0 0 / 20%);
}


.SinglePostDeleteButton:hover,
.SinglePostDeleteButton:focus,
.SinglePostDeleteButton:active {
  background-color: dodgerblue;
  cursor: pointer;
}